// Global styling for this template
body {
  font-size: 16px;
  color: $gray-900;
  @include serif-font;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  @include sans-serif-font;
}

a {
  // color: $gray-900;
  color: $black;
  @include transition-all;

  &:focus,
  &:hover {
    color: $primary;
  }
}

blockquote {
  font-style: italic;
  color: $gray-600;
}

.section-heading {
  font-size: 36px;
  font-weight: 700;
  margin-top: 60px;
}

.caption {
  font-size: 14px;
  font-style: italic;
  display: block;
  margin: 0;
  padding: 10px;
  text-align: center;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}

::-moz-selection {
  color: $white;
  background: $primary;
  text-shadow: none;
}

::selection {
  color: $white;
  background: $primary;
  text-shadow: none;
}

img::selection {
  color: $white;
  background: transparent;
}

img::-moz-selection {
  color: $white;
  background: transparent;
}

p a {
  text-decoration: underline;
}

.footer p a {
  text-decoration: underline;
}
